::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgba(0,0,0,0.1);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(0,0,0,0.4);
    border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}